import { useEffect, useState } from 'react';
import { useCanvas } from './useCanvas';
import axios from 'axios';

import warzone from './map-overview-bg.jpg';
import './App.css';

const { REACT_APP_API_URL } = process.env;

const api = axios.create({
  baseURL: REACT_APP_API_URL,
});


function App() {
  const [ menuRef, coordinates, setCoordinates, canvasRef, canvasWidth, canvasHeight, setStart, clickedContract ] = useCanvas();
  const [ currentCoord, setCurrentCoord ] = useState({})
  const [ contractCoord, setContractCoord ] = useState({})
  const [ reconCoord, setReconCord ] = useState(false)

  window.addEventListener('contextmenu', function (e) { 
    e.preventDefault(); 
  }, false);
  
  const handleCanvasClick=(event)=>{
    setStart(false)
    let screenPercentX = 100 / (canvasWidth / event.clientX);
    let screenPercentY = 100 / (canvasHeight / event.clientY);
    let _currentCoord  = {};
    if(!reconCoord)
      _currentCoord  = { x: screenPercentX, y: screenPercentY };
    else
      _currentCoord  = { parentId: clickedContract.id, x: screenPercentX, y: screenPercentY, type: 'recon' };

    setCurrentCoord(_currentCoord);
    setCoordinates([...coordinates, _currentCoord]);
  };

  const handleClearCanvas=(event)=>{
    setCoordinates([]);
  };
  const doAddContract = (event) =>{
    let screenPercentX = 100 / (canvasWidth / event.clientX);
    let screenPercentY = 100 / (canvasHeight / event.clientY);
    let _currentCoord  = { parentId: null, x: screenPercentX, y: screenPercentY, type: 'contract' };
    // add the newest mouse location to an array in state 
    api.post("",{ ..._currentCoord })
    .then(res => {
      setContractCoord(res.data)
      setCoordinates([...coordinates, res.data]);
    })

  }
  const doAddRecon = () =>{
    setReconCord(true)
    // const { x, y } = currentCoord;
    // const _currentCoord = { parentId: clickedContract.id,  x, y, type: 'recon' };

    // const canvasObj = canvasRef.current;
    // const ctx = canvasObj.getContext('2d');

    // ctx.canvas.addEventListener('mousemove', e => {
    //   ctx.save();
    //   ctx.clearRect(0, 0, canvasWidth, canvasHeight)

    //   // Get the local x/y coordinates of the mouse on the canvas
    //   let x = e.clientX;
    //   let y = e.clientY;
    //   // Draw a dot where the mouse is
    //   ctx.beginPath();
    //   ctx.arc(x, y, 10, 0, 2 * Math.PI, false);
    //   ctx.fillStyle = 'white';
    //   ctx.fill();
    //   ctx.save();
    //   ctx.restore();
    // })
    
    // console.log(_currentCoord)
    // setCoordinates([...coordinates, _currentCoord]);
  }
  const doRemove = () =>{
    const _coordinates = coordinates.filter((obj) => {
      return obj.x !== currentCoord.x && obj.y !== currentCoord.y
    })
    setCoordinates([..._coordinates]);
  }

  useEffect(() =>{
    api.get()
      .then(res => {
        const _coordinates = res.data;
        setStart(true)
        setCoordinates(_coordinates);
      })
  },[setCoordinates, setStart])
 
  return (
    <main id="container" className="App-main" >
      <img  src={warzone} className="img-warzone" alt="logo" />
  
      <canvas 
        className="App-canvas"
        ref={canvasRef}
        width={canvasWidth}
        height={canvasHeight}
        onClick={handleCanvasClick} />

      <div ref={menuRef} id="contextMenu" >
          <div className="contextMenu--contract" onClick={doAddContract} >Start Contract</div>
          { 
            clickedContract.id && 
            <div className="contextMenu--recon" onClick={doAddRecon} >Add Recon</div>
          }
          <div className="contextMenu--remove" onClick={doRemove} >Remove</div>
      </div>
       
      
      <div className="clearButton" >
        <button onClick={handleClearCanvas} > Clear Map </button>
      </div>
    </main>
  );

};

export default App;